<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process265"></a>

<p><br></p>
<p><span class="rvts17">染血的嫁妆（</span><span class="rvts50">Blood Dowry</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:425,Y:335</span><span class="rvts21">，地点：新郎多林</span><span class="rvts20"></span><span class="rvts21">格尔的炼金屋（</span><span class="rvts20">The Chemistry Lab of Dorian Gall</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助新郎盗取老丈人的资产认证书，或帮助新娘伊思拉</span><span class="rvts20"></span><span class="rvts21">格尔杀掉图财害命的新郎。</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1453.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">该支线任务的触发方式有很多种，得到的结果也会略有不同。首先，来到新郎多林的住处。他的住处实际上是一个炼金实验室。在他住处的一个小院子里，可以找到一个隐藏的按钮。</span>
<br><span class="rvts21">使用它，可以掀开他的睡床，并打开通往地下实验室的仓库门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1454.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">使用完按钮后，舱门浮现出来。注意，在有些版本中，睡床的按钮在屋子的火炉壁旁边，而不在小院子里。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1455.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">进入地下室，立马发现这里充满了油桶和毒雾，打开第一道门的方法十分简单，墙壁上有</span><span class="rvts20">3</span><span class="rvts21">个按钮，每个按钮按一遍，就可以打开所有的铁门，并解锁楼上的舱门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1456.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">来到新郎官多林的私人办公室，会发现他写的秘密日记，内容大致是写他结婚的目的在于他老丈人的财产。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1457.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">再次来到富贵的矮人婚礼处，你会发现这里经历了一场灾难，所有的宾客都被虚空怪杀掉。</span>
<br><span class="rvts21">找到新郎，揭穿他的阴谋或与他做秘密交易（偷取老丈人的资产认证书）。你可以选择是帮他偷取老丈人的遗产书，还是向他老丈人和妻子告发他。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1458.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">如果选择揭发新郎官的这种丑恶行为，他会从矮人家中逃跑，跑回自己的实验室。与他进行战斗，并杀掉他。</span>
<br><span class="rvts21">注意，如果你在矮人新娘家偷取了新郎身上的那件金色新郎魔杖，那么他会人间蒸发，该支线将无法关闭。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1459.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">之后，再次回到新娘的家中，告诉她，你已经帮他杀掉了她的夫君。新娘会感到一丝欣慰。然后找到新郎的老丈人，也就是新娘的爹（在二楼，他叫米琪尔</span><span class="rvts20"></span><span class="rvts21">罗斯），他会给予你额外的奖励。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1460.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">该任务的另一种做法就是帮助新郎官偷取老丈人米琪尔</span><span class="rvts20"></span><span class="rvts21">罗斯身上的</span><span class="rvts20"></span><span class="rvts21">遗产认证书</span><span class="rvts20"></span><span class="rvts21">，然而如果选择这样做，需要在阳台与新郎对话的时候，尽可能的选择不粗鲁的语句，并同意帮他偷遗产认证书。</span>
<br><span class="rvts21">接着，在二楼找到米琪尔</span><span class="rvts20"></span><span class="rvts21">罗斯，偷取他身上的认证书。但是，戏剧性的事情发生了，在遗产说明书上，米琪尔</span><span class="rvts20"></span><span class="rvts21">罗斯选择把所有的遗产在他死后过户给他的猪噜噜贝莉（</span><span class="rvts20">Lulabelle</span><span class="rvts21">）。此时再次找到楼下的新郎官，告诉他你已经偷到了</span><span class="rvts20"></span><span class="rvts21">遗产认证书</span><span class="rvts20"></span><span class="rvts21">。如果在偷盗过程中，被老丈人发现，那么一场战斗不可避免，但是这场战斗将导致楼下的新郎官直接毒死新娘和所有的亲朋好友。这样以来，剧情就无法得到结局。</span>
<br><span class="rvts21">当新郎官知道遗产的继承人是那只嗜睡的猪后，恼羞成怒，但他决定把这脏水往你身上泼，他会当场揭发你偷了</span><span class="rvts20"></span><span class="rvts21">自己亲爱的老丈人的遗产认证书</span><span class="rvts20"></span><span class="rvts21">，这时，所有的洋馆士兵都会变得敌对起来。此时你可以尝试一段较为困难的</span><span class="rvts20"></span><span class="rvts21">辩解</span><span class="rvts20"></span><span class="rvts21">劝说过程，如果顺利通过，则洋馆内的所有人会心灵平静下来。再次回到新郎的实验室住址，在地下室找到他，与他对峙后，一场战斗再所避免，杀掉新郎官，回矮人新娘和老丈人那里领赏（如果他们还都活着）。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



